.click-button {
    padding: 15px 35px;
    background-color: var(--background-button);
    color: white;
    font-family: "poppins-bold";
    border-radius: 100px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
}
@media screen and (max-width : 450px) {
    .click-button {
        padding: 5px 15px;
    }
}